
.login {
  padding: 20px;
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.login .demo-ruleForm {
  margin-top: 60px;
}
.login .register {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  padding: 10px;
  box-sizing: border-box;
}
.login .register .el-button {
  width: 100%;
}



