
.chat {
  width: 100%;
  height: 100%;
  position: relative;
  padding: 0 30px 0 30px;
  box-sizing: border-box;
  background: #f5f5f5;
}
.chat .chat-room {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 36px;
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 0 10px 0 10px;
  box-sizing: border-box;
}
.chat .chat-room .message-list li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: 70px;
  margin-bottom: 10px;
}
.chat .chat-room .message-list li .avatar {
  background-size: contain !important;
}
.chat .chat-room .message-list li .text {
  -ms-flex-item-align: center;
      -ms-grid-row-align: center;
      align-self: center;
  font-size: 16px;
  padding: 10px;
  border-radius: 6px;
  max-width: 90%;
  width: auto;
  word-break: break-all;
  text-align: left;
}
.chat .chat-room .message-list .receive {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.chat .chat-room .message-list .receive .text {
  background: #fff;
  margin-left: 6px;
}
.chat .chat-room .message-list .send {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
      -ms-flex-direction: row-reverse;
          flex-direction: row-reverse;
}
.chat .chat-room .message-list .send .text {
  background: #9eea6a;
  margin-right: 6px;
}
.chat .input-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}



