
.posts {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  padding: 10px 10px 0 10px;
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
}
.posts .list {
  width: 100%;
  height: 100%;
  overflow: auto;
}
.posts .add-post-page {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.7);
}
.posts .add-post-page .post-box {
  width: 90%;
  margin: auto;
  margin-top: 160px;
}
.posts .add-post-page .post-box p {
  font-size: 16px;
  color: #fff;
  text-align: left;
  margin-bottom: 6px;
}
.posts .add-post-page .post-box .title-input {
  margin-bottom: 30px;
}
.posts .add-post-page .post-box .content-input {
  margin-bottom: 30px;
}
.posts ul li.post {
  margin-bottom: 16px;
  border-bottom: 1px solid #ccc;
  padding-bottom: 6px;
}
.posts ul li.post .post-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.posts ul li.post .img {
  margin-right: 10px;
  width: 20%;
}
.posts ul li.post .img img {
  width: 100%;
}
.posts ul li.post .post-content {
  text-align: left;
  width: 90%;
  position: relative;
}
.posts ul li.post .post-content .comment-item {
  word-wrap: break-word;
  background: #eee;
  font-size: 14px;
  position: relative;
}
.posts ul li.post .post-content .comment-item .btn {
  width: 26px;
  height: 16px;
  background: #999;
  position: absolute;
  right: 0;
  top: -45px;
  border-radius: 2px;
}
.posts ul li.post .post-content .comment-item .btn span:nth-child(1) {
  position: absolute;
  background: #fff;
  left: 0;
  top: 0;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  left: 5px;
  top: 6px;
}
.posts ul li.post .post-content .comment-item .btn span:nth-child(2) {
  position: absolute;
  background: #fff;
  left: 0;
  top: 0;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  left: 15px;
  top: 6px;
}
.posts ul li.post .post-content .comment-item .btn-menu {
  position: absolute;
  top: -36px;
  right: 30px;
  background: rgba(0, 0, 0, 0.7);
  width: 136px;
  height: 30px;
  -webkit-transform-origin: right;
          transform-origin: right;
  border-radius: 6px;
}
.posts ul li.post .post-content .comment-item .btn-menu .menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.posts ul li.post .post-content .comment-item .btn-menu .menu li {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  text-align: center;
  height: 30px;
  line-height: 30px;
  width: 45px;
  color: #fff;
}
.posts ul li.post .post-content .nickname {
  color: #1c95e2;
}
.posts .comment-message-wrapper {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
.posts .comment-message-wrapper .out {
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
}
.posts .comment-message-wrapper .comment-message-box {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 36px;
  z-index: 9;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.posts .comment-message-wrapper .comment-message-box .comment-input {
  height: 100%;
}
.posts .comment-message-wrapper .comment-message-box .comment-submit {
  height: 100%;
}
.open-enter-active,
.open-leave-active {
  transition: all 0.3s;
}
.open-enter,
.open-leave-to {
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
}



