
html,
body {
  width: 100%;
  height: 100%;
}
html {
  font-size: 625%;
}
body {
  background: #ccc;
}
@media screen and (max-width: 750px) {
#app {
    width: 100%;
}
}
#app {
  box-sizing: border-box;
  font-size: 0.12rem;
  overflow: auto;
  position: relative;
  background: #f5f5f5;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  max-width: 7.5rem;
  height: 100%;
  margin: auto;
  background: #fff;
}
#app .title {
  height: 60px;
  line-height: 60px;
  background: #000;
  color: #fff;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  font-size: 16px;
  z-index: 9;
}
#app .title .add-post {
  position: absolute;
  right: 0;
  top: 0;
  width: 60px;
  height: 60px;
  background: #000;
  z-index: 99;
  color: #fff;
  font-size: 30px;
}
#app .content {
  position: absolute;
  top: 60px;
  bottom: 60px;
  overflow: auto;
  box-sizing: border-box;
  width: 100%;
}
#app .menu {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 9;
}
#app .menu ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#app .menu ul li {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  content: '';
  display: block;
  height: 0;
  clear: both;
}
* {
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
ul .item {
  margin-top: 10px;
  margin-right: 40px;
}



