
@import "../assets/css/swiper.min.css";
.self {
  width: 100%;
  height: 100%;
  overflow: auto;
  margin: auto;
  text-align: left;
  padding: 0 10px 0 10px;
  box-sizing: border-box;
}
.self .header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-bottom: 30px;
}
.self .uploader {
  text-align: left;
  display: inline-block;
  width: 160px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  height: 160px;
  vertical-align: bottom;
}
.self .uploader .el-upload.el-upload--text {
  width: 100%;
  height: 100%;
}
.self .uploader img {
  width: 100%;
  height: 100%;
}
.self .user-info {
  margin-left: 6px;
  -ms-flex-item-align: center;
      -ms-grid-row-align: center;
      align-self: center;
  width: 190px;
}
.self .user-info .nickname {
  font-size: 0.16rem;
}
.self .user-info .username {
  color: #999;
  font-size: 0.16rem;
}
.self .add_friend_btn {
  vertical-align: bottom;
  width: 100%;
  margin-top: 30px;
}
.self .slide-box {
  z-index: 1;
  text-align: center;
  vertical-align: top;
}
.self .slide-box .search_friend_box {
  padding: 30px;
}
.self .slide-box .friend-item {
  margin-bottom: 10px;
}
.self .slide-box .right {
  display: inline-block;
  vertical-align: top;
  padding-left: 30px;
}
.self .el-dialog.el-dialog--tiny {
  height: 60%;
  overflow: hidden;
}
.self .slide-box {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  bottom: 60px;
  background: #fff;
  overflow: hidden;
  padding: 30px;
  box-sizing: border-box;
}
.self .slide-box .back-btn-wrapper {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 60px;
}
.self .slide-box .back-btn-wrapper .back-btn {
  width: 100%;
  height: 100%;
}
.self .request .request-item {
  border-bottom: 1px solid #ccc;
}
.self .logout-wrapper .logout_btn {
  width: 100%;
}
.self .avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.self .avatar-uploader .el-upload:hover {
  border-color: #20a0ff;
}
.self .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 100%;
  height: 100%;
  line-height: 178px;
  text-align: center;
}
.self .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.self .swiper-container {
  position: absolute;
  left: 0;
  top: 90px;
  bottom: 60px;
  width: 100%;
  overflow: hidden;
}
.self .swiper-slide {
  font-size: 18px;
  height: auto;
  box-sizing: border-box;
  font-size: 13px;
  font-family: microsoft yahei;
  line-height: 1.8;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}



